  docx: "\f1c2",
  docm: "\f1c2",
  eps: "\f1c5",
  gif: "\f1c5",
  jpeg: "\f1c5",
  jpg: "\f1c5",
  pdf: "\f1c1",
  png: "\f1c5",
  ppt: "\f1c4",
  pptx: "\f1c4",
  tiff: "\f1c5",
  xls: "\f1c3",
